
.header-language-selector {

}

    .header-language-selector__selectable {
        opacity: .5;
    }

@include media-query(small-up) {
    .header-language-selector {
        position: relative;
        display: inline-block;
    }
}

@include media-query(small) {
    .header-language-selector {
        position: absolute;
        top: 2.75rem;
        right: 1.5rem;
        max-width: 7rem;
        text-align: right;
    }
}
