
.tab-nav {
    display: flex;
    position: relative;
    margin-bottom: 2rem;
    box-shadow: inset 0 -.0625rem 0 0 $color-grey-4;
}

    .tab-nav__item {
        padding-top: .5em;
        padding-bottom: .75em;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: .5;
        font-size: $font-size-deci;
        transition-property: box-shadow, color;
        transition-duration: $transition-speed-fast;
        transition-timing-function: $transition-ease-bounce;
        cursor: pointer;
        opacity: .5;

        &:hover:not(.toggle--active) {
            opacity: 1;
        }

        //Only grow to use all available space when used within a .tab-nav container.
        .tab-nav & {
            flex: 1 1 50%;
            max-width: 50%;
        }

        &.toggle--active,
        .tab-nav__item-check.toggle--active + & {
            opacity: 1;
            box-shadow: inset 0 -.1875rem 0 0 $color-secondary;
        }
    }

    .tab-nav__item-check {
        position: absolute;
        opacity: 0;
    }

.tab-content {
    position: relative;
}

