
.product-attribute__list {
    //Remove bottom margin to offset the difference added by the individual
    //.product-attribute elements.
    margin-bottom: -1rem;

    //This specifity is intentional so that it is easier to separate
    //different types of "product-attribute__list" container.
    //For example, one may prefer to have a different layout for QuickAdd, Bundles, etc.
    .product-attribute {
        flex: 1 1 100%;
        margin-bottom: 1rem;
    }

    .product-attribute--color {
        margin-bottom: 2.5rem;

        .product-attribute__contents {
            margin-top: -1.5rem;
        }
    }

    .product-attribute--qty {
        flex-basis: 6.375rem;
        max-width: 6.375rem;
    }

        .product-attribute__swatch {
            flex: 0 0 5.5rem;
            width: 5.5rem;
            height: 5.5rem;
            margin-top: 1.5rem;
            margin-bottom: 1rem;

            &:not(:last-child) {
                margin-right: 1rem;
            }
        }

        .product-attribute__selected-value {
            display: block;
            margin-top: .5rem;
        }
}

.product-attribute__list--check-availability {
    margin-top: 2rem;

    .product-attribute__swatch {
        flex: 0 0 4rem;
        width: 4rem;
        height: 4rem;
    }
}

    .product-attribute__swatch {
        position: relative;

        &:after {
            position: absolute;
            top: 0;
            right: 0;
            width: 0;
            height: 100%;
            box-shadow: 0 .25rem 0 $color-secondary;

            transition-property: width;
            transition-duration: $transition-speed-fast;
            transition-timing-function: $transition-ease-in;

            content: '';
        }

        &:not(.disabled):hover, &.selected {
            &:after {
                left: 0;
                width: 100%;

                transition-timing-function: $transition-ease-out;
            }
        }

        &.disabled, &.unselectable {
            opacity: 0.5;
            cursor: not-allowed;
        }
    }

    .product-attribute__size {
        [data-lang="de"] & {
            text-transform: initial;
        }
    }

        .product-attribute__swatch-label {
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            margin-top: .5rem;
            text-align: center;
        }

    .product-availability__label {
        margin-right: .25em;
    }

    .product-attribute__size-chart-trigger {
        margin-bottom: 1rem;
    }

.product-gallery__img {
    max-height: none;
    width: 60%;
    height: 60%;
    &.full-stretch-image {
        width: 100%;
        height: 100%;
    }
}

.product-promotions__item {
    word-break: break-word;
}

// .product-pickup {
//     margin-top: 1rem;
// }

    .product-pickup__selected {
        padding-left: 1.9em;
        margin-top: .5rem;
    }

    .product-pickup__selected-action {
        margin-top: .75rem;
    }

.product-common__wishlist-trigger {
    width: 2.8125rem;
    height: 2.1875rem;
    margin-left: 1rem;
    border: solid .0625rem $color-primary;
    transition: color $transition-speed-normal $transition-ease-bezier,
                border-color $transition-speed-normal $transition-ease-bezier;

    &:hover, &:focus {
        color: $color-secondary;
    }

    &.set--in-wishlist {
        .product-common__wishlist-add {
            display: none;
        }

        .product-common__wishlist-remove {
            display: block;
        }
    }
}

    .product-common__wishlist-remove {
        display: none;
    }

.product-common__social {
    margin-top: 4rem;
}

    .product-common__social-item {
        &:not(:last-child) {
            margin-right: 2.5rem;
        }
    }

.product-add__container {
    flex-wrap: wrap;
    .product-get__notify {
        flex: 0 0 100%;
        margin-bottom: 1.5rem;        
    }
    p {
        margin-bottom: 0.5rem;
    }
}

.brand__panther-image {
    &.image-width__small {
        img {
            max-width: 5rem;
            margin: 0 auto;
        }
    }
    &.image-width__large {
        img {
            max-width: 8.125rem;
            margin: 0 auto;
        }
    }
}

.pdp {
    .brand__panther-image {
        padding-bottom: 6rem;
    }
}

.search-results__footer {
    .brand__panther-image {
        padding-bottom: 0.938rem;
    }
}

.cart {
    .brand__panther-image {
        max-width: 77.5rem;
        margin: 0 auto;
        &.inverted {
            padding: 0 0.9375rem 1rem;
            img {
                transform: scaleX(-1);
                max-width: 3.75rem;
                margin: 0 auto 0 0;
            }
            @include media-query(small-up) {
                padding: 0 0 2rem;
                img {
                    max-width: 5rem;
                }
            }
        }
    }
}

.product-add__button {
    &.button--apple-pay {
        &, &:hover, &:active {
            &:not(:first-child) {
                margin-top: 1rem;
            }
        }
    }
}

@include media-query(small-up) {
    .product-attribute__list {
        .product-attribute--last:not(.product-attribute--color) {
            // max-width: 11.5rem;
        }
    }
}
