

@include media-query(medium-up) {
    .footer__country-selector-anchor {
        margin-left: .75rem;
    }
}

@include media-query(small-up) {
    .footer {

    }

    .footer__main-content--checkout {
        border-top: solid .0625rem $color-grey-3;
    }

        .footer__main-content {
            margin-top: 2.75rem;
            margin-bottom: 3rem;
        }

        .footer__main-content--checkout {
            //Uses padding to account for border styles.
            padding-top: 2.25rem;
            margin-bottom: 2.5rem;
        }

        .footer__secondary-content {
            margin-top: 2.5rem;
            padding-top: 1.5rem;
            padding-bottom: 1.5rem;
        }
}

@include media-query(medium) {
    .footer__country-selector-anchor {
        margin-top: .5rem;
    }
}

@include media-query(small) {
    .footer {

    }

        .footer__separator {
            margin-top: 3.125rem;
            margin-bottom: 3.125rem;
        }

        .footer__main-content {
            margin-top: 2.25rem;
        }

        .footer__main-content--checkout {
            margin-bottom: 2.625rem;
        }

        .footer__secondary-content {
            margin-top: 1.25rem;
            padding-top: 2.25rem;
            padding-bottom: 2.25rem;
        }

        .footer__secondary-content--checkout {
            padding-bottom: 5.5rem;
        }

        .footer__secondary-content-column {
            &:not(:last-child) {
                margin-bottom: 3rem;
            }
        }
}

.usp-footer-banner {
    margin-top: 20px;
}

.footer {
    z-index: 9;
}
