

.product-badge-list--floating {
    position: absolute;
    top: .625rem;
    left: .625rem;
    z-index: 1;
}

.product-badge-list--full-product {
    margin-bottom: 1rem;
}

.product-badge-list--line-item {
    margin-bottom: .5rem;
}

.product-badge {
    display: inline-block;
    
    &:not(:last-child) {
        margin-bottom: .5em;
    }
}

.product-badge--bordered {
    background-color: $color-white;
    border: solid .0625rem $color-grey-4;
    padding: .6em .75em;
}

.product-badge--novelty {
    font-size: .75rem;
    font-weight: 400;
    text-transform: uppercase;
    background-color: #fff;
    z-index: 1;
    position: absolute;
    padding: 2px;
}

.product-tile .product-badge--novelty { 
    display: flex;
    justify-content: center;
    left: 8px !important;
    top: 8px;
}

.pdp-main__media .product-badge--novelty {
    left: 3.5rem;
    top: 4.2rem;
    padding: 4px 8px;
}

.product-gallery--pdp {
    position: relative;
}
