
.product-tile {
    min-height: 27rem;

    // To handle slick-slider without left/right arrows
    .slick-list:only-child & {
        min-height: 30rem;
    }

    .price {
        .tiered {
            font-size: 0.875em;

            .value {
                font-weight: bold;
            }
        }
    }

    &.set--quickadd-active {
        pointer-events: none;
    }
}

.product-tile--default {
    position: relative;

    .product-tile__anchor {
        display: block;
    }

    .product-tile__body {
        padding-top: 1rem;
    }

    .product-tile__media {
        display: block;
        overflow: hidden;

        @include aspect-ratio(1, 1, true);
    }

        .product-tile__media-container {
            padding: 1rem;
        }

        .product-tile__image {
            width: 60%;
            height: 60%;

            &.full-stretch-image {
                width: 100%;
                height: 100%;
            }

            &.image-zoom__lg:not(.full-stretch-image) {
                width: 80%;
                height: 80%;
            }
        }

    .product-tile__quickadd {
        margin-top: 1rem;
    }
}

.product-tile--overlay {
    padding: 1.25rem;
}

.product-tile--card {
    position: relative;
    margin-bottom: 1.25rem;

    .product-tile__body {
        flex: 1 1 70%;
        padding-right: .5rem;
        padding-left: .5rem;
    }

    .product-tile__media {
        display: block;
        overflow: hidden;
        flex: 1 1 30%;
    }
}

    .product-tile__body {
        position: relative;
    }

    .product-tile__media {
        background-color: $color-grey-1;
    }

    .bg--grey-image {
        background-image: url('../images/fond-beige-horizontal.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-color: transparent;
    }

    .product-tile__body-section {
        &:not(:first-child) {
            margin-top: .5rem;
        }
    }

    .product-tile__swatches {

    }

        .product-tile__swatch {
            &:not(:last-child) {
                margin-right: .5rem;
            }
        }

    .product-tile__quickview {
        position: absolute;
        top: 0;
        right: 1rem;
        //This margin-top value must match the aspect ratio of the
        //product tile's media so that the CTA is aligned properly.
        margin-top: 100%;
        transform: translateY(calc(-100% - 1rem));
        opacity: 0;

        .product-tile:hover &,
        &:focus {
            opacity: 1;
        }
    }

    .product-tile__quickadd {
        position: relative;
        z-index: 1;

        &.set--added-to-cart {
            .product-tile__quickadd-trigger {
                // @include set-invisible();
            }

            .product-tile__quickadd-actions {
                display: block;
            }
        }
    }

        .product-tile__quickadd-panel {
            z-index: 2;
            position: absolute;
            bottom: -.5rem;
            left: -.5rem;
            width: calc(100% + 1rem);
            padding: 1rem;
            background-color: $color-panel-background;
            border: solid .0625rem;
            border-radius: .25rem;
            box-shadow: 0 .25rem .75rem transparentize($color-black, .75);
            transform: translateY(0);
            //Allow pointer events only in this container.
            pointer-events: all;

            transition-property: opacity, visibility, transform;

            &.set--quickadd-active {
                transition-duration: $transition-speed-normal;
                transition-timing-function: $transition-ease-bezier;
            }

            &:not(.set--quickadd-active) {
                transition-duration: $transition-speed-fast;
                transition-timing-function: $transition-ease-in;
            }

            &:not(.set--ready) {
                transform: translateY(.5rem);
                @include set-invisible();
            }
        }

        .product-tile__quickadd-actions {
            display: none;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            background: $color-white;
            border: solid .0625rem;
            border-radius: $input-border-radius;
        }

    .product-tile__wishlist {
        display: flex;
        position: absolute;
        top: 0;
        right: 1rem;
        padding: .5em;
        border-radius: 50%;
        overflow: hidden;

        //This margin-top value must match the aspect ratio of the
        //product tile's media so that the CTA is aligned properly.
        margin-top: 100%;

        // Stacking transforms for IE11 support
        // instead of `translateY(calc(-100% - 1rem))`
        transform: translateY(-100%) translateY(-1rem);

        &.set--in-wishlist {
            .product-tile__wishlist-add {
                display: none;
            }

            .product-tile__wishlist-remove {
                display: block;
            }
        }
    }

        .product-tile__wishlist-remove {
            display: none;
        }

    .product-tile__refill-wrapper {
        min-height: calc(100vh - 12rem);

        .product-tile__media-container {
            margin: .5rem;
        }

        .product-tile__image {
            width: auto; //IE11 image stretch fix
        }
    }

        .product-tile__refill {
            margin-bottom: 1.5rem;
        }

        .product-tile__refill-check-label {
            position: absolute;
            right: 1rem;
            bottom: 1.5rem;
        }

@include media-query(small-up) {
    .product-tile--card {
        padding-top: 1rem;
        padding-bottom: 1rem;
        padding-right: $gutter-outer;
        padding-left: .5rem;
    }

    .product-tile {
        z-index: 0;
        transition-property: z-index;
        transition-duration: 0s;
        transition-delay: $transition-speed-fast;

        &:hover {
            z-index: 2;
            transition-delay: 0s;

            .product-tile__overlay {
                @include set-visible();

                &:after {
                    width: 100%;
                    height: 100%;
                }
            }

            .product-tile__image--primary {
                transform: scale(1.1);
            }

            .product-tile__image--secondary {
                transform: scale(1.15);
            }
        }
    }

        .product-tile__anchor {
            position: relative;
        }

        .product-tile__anchor,
        .product-tile__wishlist {
            z-index: 1;
        }

        .product-tile__wishlist {
            opacity: 0;

            transition-property: opacity;
            transition-duration: $transition-speed-fast;
            transition-timing-function: $transition-ease-out;

            .product-tile:hover &,
            &:focus {
                opacity: 1;
                transition-timing-function: $transition-ease-in;
            }
        }

        .product-tile__overlay {
            z-index: 0;
            position: absolute;
            top: -1.5rem;
            left: 50%;
            transform: translateX(-50%);
            width: calc(100% + 3rem);
            height: calc(100% + 3rem);
            padding: 1rem 1rem 2rem;

            transition-property: opacity, visibility;
            transition-duration: $transition-speed-fast;
            transition-timing-function: $transition-ease-bezier;

            @include set-invisible();

            .product-grid & {
                height: calc(100% + 7.5rem);
            }

            &:after {
                width: 85%;
                height: 85%;
                border: solid .0625rem $color-grey-3;

                transition-property: width, height;
                transition-duration: $transition-speed-fast;
                transition-timing-function: $transition-ease-bezier;

                @include vertical-align(true, true);

                content: '';
            }
        }

        .product-tile__anchor:focus,
        .product-tile--default:hover {
            .product-tile__image {
                transition-timing-function: $transition-ease-out;
            }

            .product-tile__image--secondary.lazyloaded {
               @include set-visible();
            }
        }

        .product-tile__refill-check {
            align-items: flex-end;
            align-self: center;
        }

        //The "set--images-lazy-loaded" helper class is set in JS when lazysizes detects that the secondary images are loaded.
        //This prevents displaying a blank square while the secondary images load.
        .set--images-lazy-loaded .product-tile__anchor:focus,
        .set--images-lazy-loaded.product-tile--default:hover {
            .product-tile__image--primary.set--has-secondary-image {
                @include set-invisible();
            }
        }

            .product-tile__image {
                max-height: none;
                transition-property: opacity, visibility, transform;
                transition-duration: $transition-speed-fast;
                transition-timing-function: $transition-ease-in;
            }

            .product-tile__image--secondary {
                @include set-invisible();
            }
}

@include media-query(small) {
    .product-tile--card {
        padding: 1.25rem;

        .product-tile__footer {
            margin-top: 1.25rem;
        }
    }

    .product-tile__overlay {
        margin-top: auto;
    }

        .product-tile__overlay-actions {
            margin-top: .5rem;
        }
}
