/*------------------------------------*\
  #ICONS - Mapped to the static svg directory file names
\*------------------------------------*/

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;

    //In case the .icon class is not added directly on the svg element.
    svg {
        width: 100%;
        height: 100%;
        fill: inherit;
    }
}

.icon--small {
    width: .5rem;
    height: .5rem;
}

.icon--medium {
    width: .85rem;
    height: .85rem;
}

.icon--large {
    width: 1.5rem;
    height: 1.5rem;
}

.icon--rotate-h {
    //Mirror and not rotate. Rotation can cause a slightly off rendering.
    transform: scaleX(-1);
}

.icon--rotate-up {
    transform: rotate(90deg);
}

.icon--rotate-down {
    transform: rotate(-90deg);
}

.icon--set-left {
    margin-right: 1em;
}

.icon--set-left-short {
    margin-right: .75em;
}

.icon--set-left-large {
    margin-right: 1.5em;
}

.icon--set-right {
    margin-left: 1em;
}

.icon--set-right-short {
    margin-left: .75em;
}

.icon--set-right-large {
    margin-left: 1.5em;
}

.icon--inline-align-center {
    vertical-align: middle;
}
